import React from 'react';
import {
  Grid,
  TextField,
  SvgIcon,
  InputAdornment,
  IconButton,
} from '@material-ui/core';
import { makeStyles } from '@material-ui/core/styles';
import ToggleIcon from 'material-ui-toggle-icon';
import { checkIcon, alertIcon } from '../../assets/svg/SvgIcons';

const useStyles = makeStyles((theme) => ({
  stepperInputContainer: { margin: `0 ${theme.spacing(1)}` },
}));

const InputTextRenderer = (props) => {
  const classes = useStyles();
  return props.formList.map((formItem) => {
    const {
      id,
      fieldName,
      config,
      formObject,
      formChangeHandler,
      objectList,
      index,
    } = { ...formItem };
    return (
      <Grid item key={id} className={classes.stepperInputContainer}>
        <TextField
          key={id}
          name={id}
          id={id}
          label={config.label}
          variant="outlined"
          color="secondary"
          margin="normal"
          type={config.type}
          required={config.validations.isRequired}
          error={config.isTouched && !config.validity.isValid}
          value={config.value}
          multiline={config.multiline}
          maxRows={4}
          minRows={4}
          disabled={config.disabled}
          readOnly={config.disabled}
          style={{ width: 300 }}
          inputProps={{ 'data-index': index }}
          onChange={formChangeHandler({
            fieldName,
            formObject,
            objectList,
            index,
            changeType: 'change',
          })}
          onBlur={formChangeHandler({
            fieldName,
            formObject,
            objectList,
            index,
            changeType: 'change',
          })}
          helperText={
            config.isTouched && config.validity.isValid ? (
              <span>
                <SvgIcon
                  style={{
                    width: 12,
                    height: 12,
                    marginRight: 8,
                  }}
                >
                  <path d={checkIcon.path} />
                </SvgIcon>
                {config.validity.label}
              </span>
            ) : config.isTouched && !config.validity.isValid ? (
              <span>
                <SvgIcon
                  style={{
                    width: 12,
                    height: 12,
                    marginRight: 8,
                  }}
                >
                  <path d={alertIcon.path} />
                </SvgIcon>
                {config.validity.label}
              </span>
            ) : (
              '*Required'
            )
          }
          InputProps={{
            endAdornment: (
              <InputAdornment>
                {config.icon.type === 'toggle' ? (
                  <IconButton
                    disabled={Boolean(!config.value) || config.disabled}
                    onClick={formChangeHandler({
                      fieldName,
                      formObject,
                      objectList,
                      index,
                      toggleType: 'passwordText',
                    })}
                  >
                    <ToggleIcon
                      on={config.icon.isOn}
                      onIcon={
                        <SvgIcon>
                          <path d={config.icon.on.path} />
                        </SvgIcon>
                      }
                      offIcon={
                        <SvgIcon>
                          <path d={config.icon.off.path} />
                        </SvgIcon>
                      }
                    />
                  </IconButton>
                ) : (
                  <IconButton
                    disabled={Boolean(!config.value) || config.disabled}
                    onClick={formChangeHandler({
                      fieldName,
                      formObject,
                      objectList,
                      index,
                      changeType: 'reset',
                    })}
                  >
                    <SvgIcon>
                      <path d={config.icon.close.path} />
                    </SvgIcon>
                  </IconButton>
                )}
              </InputAdornment>
            ),
          }}
        />
      </Grid>
    );
  });
};

export default InputTextRenderer;
